@use 'base/variables' as v;
@use 'base/mixins' as m;

.acciones {
  @include m.tablet {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  a {
    color: v.$blanco;
    margin-top: calc(v.$separacion / 2);
    
    @include m.tablet {
      margin-top: 0;
    }

    display: block;
    text-align: center;
  }
}
