@use 'base/variables' as v;
@use 'base/mixins' as m;

.tabs {
  @include m.tablet {
    display: flex;
  }

  button {
    background-color: v.$azul;
    color: v.$blanco;
    border: none;
    padding: 1rem;
    width: 100%;
    margin-bottom: 1rem;
    font-weight: v.$bold;
    text-transform: uppercase;
    transition: background-color 300ms ease;

    &:hover {
      cursor: pointer;
      background-color: darken(v.$azul, 10%);
    }

    // Edicion de seleccion de botones
    &.actual {
      background-color: v.$blanco;
      color: v.$azul;
    }
  }
}