@use 'base/variables' as v;

.alerta {
  padding: calc(v.$separacion / 4);
  margin-bottom: 2rem;
  width: 100%;
  display: block;
  color: v.$blanco;
  text-align: center;
  font-weight: v.$bold;

  &.error {
    background-color: v.$rojo;
  }

  &.exito {
    background-color: v.$verde;
  }
}